<template>
    <div class="chat">
        <div class="chat__header">
            <span class="chat__header__greetings">
                안녕하세요. {{ userData.userName }}님!
            </span>
        </div>

        <chat-list :msgs="msgs"></chat-list>
        <chat-form @submitMessage="sendMessage"></chat-form>
    </div>
</template>

<script>
    export default {
        props: ["meId", "jwtToken", "userId"],
        data() {
            return {
                getChatHistoryUrl: "https://localhost:6868/Chat/fromId/and/toId",
                msgs: null,
            };
        },
        created() {

        },
        async mounted() {
            await this.getChatHistory();
        },
        methods: {
            async getChatHistory() {
                await this.$http.get(
                    this.getChatHistoryUrl.replace("fromId", this.meId).replace("toId", this.userId)
                ).then(res => {
                    this.msgs = res.data;
                }).catch(res => {
                    console.log(res.response);
                });
            },
            sendMessage(msg) {
            },
        },
    };
</script>

<style scoped>
    .chat {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .chat__header {
        background: #ffffff;
        box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.05);
        border-radius: 24px 24px 0px 0px;
        padding: 1.8rem;
        font-size: 16px;
        font-weight: 700;
    }

    .chat__header__greetings {
        color: #292929;
    }
</style>